@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700);
*, *:before, *:after {
  box-sizing: border-box;
}
html {
  font-size: 16px;
  background-color: #fff;
}
body {
  color: #4a4a4a;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.5;
  margin: 0;
}
#root, .wrapper {
  height: 100%;
}
body, button, input {
  font-family: 'Open Sans', BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}
h1, h2, h3 {
  font-weight: 700;
  margin-top: 0;
}
a {
  text-decoration: none;
  color: #3366ff;
}
input[type="search"], input[type="text"], input[type="password"] {
  width: 100%;
  border: 1px solid #e8e8e8;
  padding: 3px 10px;
  height: 30px;
}
input[type="search"]:focus, input[type="text"]:focus, input[type="password"]:focus {
  background-color: #f9f9f9;
}
.wrapper {
  max-width: 960px;
  margin: 0 auto;
}
.container {
  padding-left: 15px;
  padding-right: 15px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}
.header h2 {
  margin: 0;
}
.header input[type="search"] {
  width: 400px;
}
.header button {
  margin-left: 1rem;
}
.button {
  border: 1px solid #e8e8e8;
  background-color: #fff;
  border-radius: 3px;
  height: 30px;
  padding: 0 10px;
  cursor: pointer;
}
.modal {
    display: block;
    background-color: rgba(0,0,0,.5);
}
.product-list {
    list-style: none;
    margin: 20px 0;
    padding: 0;
    background: #fff;
    border-radius: 3px;
    border: 1px solid #e8e8e8;
    position: relative;
}
.product-list li {
    border-bottom: 1px solid #e8e8e8;
    display: block;
    position: relative;
    background: inherit;
    color: inherit;
}
.product-list li:hover {
    background-color: #f9f9f9;
}
.product {
    padding: 20px;
    display: flex;
}
.thumbnail {
    margin-right: 10px;
}
.thumbnail img {
    width: 80px;
    height: 80px;
}
.content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}
.content h3 {
    font-size: 16px;
    font-weight: 600;
    margin: 0;
}
.content p {
    font-size: 13px;
    font-weight: 400;
    color: #717171;
    margin-top: 0;
}
.content .cat {
    font-size: 12px;
}
.button-box {
    width: 64px;
    margin-left: 12px;
    border-radius: 3px;
    border: 1px solid #e8e8e8;
    background-color: #fff;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 13px;
}
.button-box button {
    border: none;
    background-color: transparent;
    font-weight: 700;
    font-size: 16px;
    padding: 5px 10px;
}
.button-box button:hover {
    background-color: #f9f9f9;
    cursor: pointer;
}
.button-box button:first-child:hover {
    background-color: green;
    color: #fff;
}
.button-box button:last-child:hover {
    background-color: red;
    color: #fff;
}

